import { useMediaQuery, useTheme } from '@material-ui/core'
import React from 'react'
import {
  Filter,
  FilterProps,
  List,
  Pagination,
  SelectInput,
  TextInput,
  ListProps
} from 'react-admin'
import { MobilePostPagination } from '../common/Pagination'
import { DateFilterInput } from './DateFilterInput'
import { LogGrid } from './LogGrid'

interface LogListProps extends ListProps {
  readonly compactMode?: boolean
  readonly noBooking?: boolean
}

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput label="Kod biletu" source="ticketCode" alwaysOn />
    <SelectInput
      label="Rodzaj urzdzenia"
      source="deviceName"
      choices={[
        { id: 'Czytnik', name: 'Czytnik' },
        { id: 'Przej\u015Bcie', name: 'Przej\u015Bcie' },
      ]}
    />
    <DateFilterInput source="date" key="date" />
    <TextInput source="bookingId" />
    <TextInput source="event" />
  </Filter>
)

export const LogList = ({compactMode = false, noBooking = false, ...props}: LogListProps) => {
  const theme = useTheme()
  const smallScreen = useMediaQuery(theme.breakpoints.down('sm'))
  return (
    <List
      perPage={20}
      exporter={false}
      pagination={
        smallScreen ? (
          <MobilePostPagination />
        ) : (
          <Pagination rowsPerPageOptions={[20, 50, 100]} />
        )
      }
      filters={<ListFilters />}
      sort={{ field: 'date', order: 'DESC' }}
      {...props}
    >
      <LogGrid compactMode={compactMode} noBooking={noBooking} />
    </List>
  )
}

LogList.defaultProps = {
  compactMode: false,
  noBooking: false,
} as LogListProps