import { Switch, Typography } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import { Brightness3, Brightness5 } from '@material-ui/icons'
import React from 'react'
import { AppBar, CoreLayoutProps, Layout } from 'react-admin'
import { useDispatch, useSelector } from 'react-redux'
import { DarkTheme, LightTheme } from '../../app.theme'
import { ThemeState } from '../../core/common/theme.reducer'
import { CHANGE_THEME } from '../../core/constants/reducer.actions'

const useStyles = makeStyles({
  title: {
    flex: 1,
    textOverflow: 'ellipsis',
    whiteSpace: 'nowrap',
    overflow: 'hidden',
  },
  spacer: {
    flex: 1,
  },
})

const AppTopBar = (props: any) => {
  const classes = useStyles()
  const dispatch = useDispatch()
  const theme = useSelector((state: ThemeState) => state.theme.darkMode === 'dark')

  return (
    <AppBar {...props}>
      <Typography
        variant="h6"
        className={classes.title}
        id="react-admin-title"
      />
      <Switch checkedIcon={<Brightness3 fontSize="small" />} icon={<Brightness5 fontSize="small" />} checked={theme}
              style={{ color: 'black', backgroundColor: 'transparent' }} color="primary"
              onChange={() => dispatch({ type: CHANGE_THEME })} />
    </AppBar>
  )
}

export const AppLayout = (props: CoreLayoutProps) => {
  const theme = useSelector((state: ThemeState) => state.theme.darkMode === 'dark' ? DarkTheme : LightTheme)

  return (
    <Layout
      {...props}
      appBar={AppTopBar}
      theme={theme} />)
}